import React from 'react';

const Checkbox = ({
  input,
  label,
  type,
  name,
  meta: { touched, error, warning }
}) => (
  <div>
    <input {...input} placeholder={label} type={type} id={name} name={name}  />
    <label htmlFor={name}> {label}</label>
    {touched &&
      ((error && <div>{error}</div>) ||
        (warning && <div>{warning}</div>))}
  </div>
)

export default Checkbox;
